const charts = ["class", "frame","battery_size","battery_connector","battery_brand","battery_voltage","motor_brand","motor_stator","motor_kv","motor_scale","motor_screws","props","fc_brand","firmware","esc_firmware","esc_pwm", "external_vtx","camera","camera_angle", "camera_mount","antenna","polarity","leds","weight","flight_mode","goggles","vrx","radio","control_link","failsafes","stick_ends","flight_style","video_quality","spotter","velocidrone"];
const surveyQuestions = ["name", "frame_raw","battery_size","battery_connector","battery_brand_raw","battery_voltage","motors_raw","motor_scale_raw","motor_screws","props_raw","fc_raw","firmware_raw","esc_raw","external_vtx","camera","camera_angle_raw","camera_mount_raw","antenna","polarity","leds","weight","flight_mode","rates","cli_dump","goggles_raw","radio_raw","control_link_raw","failsafes","stick_ends_raw","flight_style","weight_savings","breakage","video_quality","spotter","velocidrone","additional_comments"];
let filters = [];


const IndexPage = () => {
  const [filteredAnswers, updateAnswers] = useState(answers);
  const [showSurvey, toggleSurvey] = useState(false);

  const chartEvents = [
    {
      eventName: "select",
      callback: ({ chartWrapper }) => {
        const chart = chartWrapper.getChart();
        const selection = chart.getSelection();
        const dataTable = chartWrapper.getDataTable();
        const filterVar = dataTable.getColumnLabel(0);
        if (selection.length === 0) {
          for(let i = 0; i < filters.length; i++)
          {
            const filter = filters[i];
            if(filter[0] === filterVar)
            {
              filters.splice(i,1);
              break;
            }
          }
        }
        if (selection.length === 1) {
          const filterVal = dataTable.getValue(selection[0].row,0);
          let filterFound = false;
          for(let i = 0; i < filters.length; i++)
          {
            const filter = filters[i];
            if(filter[0] === filterVar && filter[1] === filterVal)
            {
              filters.splice(i,1);
              filterFound = true;;
              break;
            }
            if(filter[0] === filterVar)
            {
              filter[1] = filterVal;
              filterFound = true;
              break;
            }
          }
          if(!filterFound) {
            filters.push([filterVar, filterVal]);
          }
        }
        filtersUpdated();
      },
    },
  ];

  function resetFilters() {
    filters = [];
    filtersUpdated();
  }
  function filtersUpdated() {
    let newAnswers = JSON.parse(JSON.stringify(answers));
    for(const filter of filters)
    {

      newAnswers = newAnswers.filter(function (answer) {
        return answer[filter[0]] === filter[1] || answer[filter[0]] === newAnswers[0][filter[0]];
      })
    }
    console.dir(filters);
    console.dir(newAnswers);
    updateAnswers(newAnswers);
  }
  function RenderChart(chartKey){
    const chartOptions = {
      title: filteredAnswers[0][chartKey].replace("(C)","")
    }
    const chartData = [];
    for(let i = 1; i < filteredAnswers.length; i++)
    {
      if(filteredAnswers[i][chartKey] === "")
          continue;
      let found = false;
      for(let j = 0; j < chartData.length; j++)
      {
        if(chartData[j][0] === filteredAnswers[i][chartKey])
        {
          chartData[j][1]++;
          found = true;
        }
      }
      if(!found)
      {
        chartData.push([filteredAnswers[i][chartKey], 1]);
      }
    }

    chartData.sort((a,b) => b[1] - a[1]);

    chartData.unshift([chartKey, "Count"]);
  
  return (<div><Chart
    chartType="PieChart"
    id={chartKey}
    data={chartData}
    options={chartOptions}
    chartEvents={chartEvents}
    width={"40vw"}
    height={"400px"}
  /></div>);
  }

  function RenderPilot(answer){
    if(answer["name"] === "Pilot Name")
      return "";
      let pilotButtonStyle;
    switch(answer["class"]) {
      case "Elite":
        pilotButtonStyle = eliteButtonStyle;
        break;
      case "Pro":
        pilotButtonStyle = proButtonStyle;
        break;
      case "Advanced":
        pilotButtonStyle = advButtonStyle;
        break;
      case "Hobbyist":
        pilotButtonStyle = hobButtonStyle;
        break;
      default:
        pilotButtonStyle = entButtonStyle;
        break;
    }
    return (<li><button onClick={() => {
      filterToPilot(answer["name"]);
    }} style={pilotButtonStyle}>
      {answer["name"]}
    </button></li>);
  }

  function filterToPilot(pilot){
    let found = false;
    for(let i = 0; i < filters.length; i++) {
      if(filters[i][0] === "name") {
        filters.splice(i, 1);
        toggleSurvey(false);
        found = true;
        break;
      }
    }
    if(!found)
    {
      filters.push(["name",pilot]);
      toggleSurvey(true);
    }
    filtersUpdated();
  }

  function renderAnswers() {
    let nameFilterActive = false;
    for(const filter of filters) {
      if(filter[0] === "name") {
        nameFilterActive = true;
        break;
      }
    }
    console.dir(filters);
    console.dir(nameFilterActive);
    if(!nameFilterActive || filteredAnswers.length > 2)
      return "";
    return (
      <div>
      {surveyQuestions.map(question => (
        <><b>{filteredAnswers[0][question]}</b><br />
        {(() => {
          if(question === "cli_dump" && filteredAnswers[1][question] !== "")
          {
            return <a href={filteredAnswers[1][question]}>I agree to use this to see setups and not mindlessly flash this to my whoops</a>
          } else {
            return filteredAnswers[1][question];
          }
        })()}
        <br /><br /></>
      ))}
      </div>
    )

  }

  function renderSurvey () {
    if(showSurvey === false)
      return "";
    else
      return <div class="surveyArea" style={surveyStyles} >
      <h1><button onClick={() => {
        toggleSurvey(false);
      }}>X</button> Survey Answers</h1>
      {
        renderAnswers()
      }
    </div>;
  }

  return (
    <main style={pageStyles}>
        <h1>Whooptopia 2023 Fleet Survey<button onClick={() => {
          resetFilters();
        }} style={resetStyle}>Reset Filters</button></h1>
      <div class="wrapper" style={gridStyles}>
        <div class="chartArea" style={summaryStyles}>
          {charts.map(chart => (
          RenderChart(chart)
          ))}
        </div>
        <div class="pilotArea" style={pilotStyles}>
          <h1>Pilots</h1>
          <ul style={pilotListStyle}>
          {filteredAnswers.map(answer => (
            RenderPilot(answer)
          ))}
          </ul>
        </div>
      </div>
      {renderSurvey()}
    </main>
  )
}

export default IndexPage

export const Head = () => <title>Whooptopia 2023 Pilot Survey</title>
